<template>
  <div class="adaptabilitylist">
    <v-card class="mt-2">
      <v-card-title>
        <v-row>
          <v-col cols="12" md="8">
            <h2 class="subheading grey--text">
              <v-icon class="mx-1">fas fa-file-alt</v-icon>
              <span>
                {{this.$store.state.uData.p_code}} /
                {{this.$store.state.uData.name}}
              </span>
              <span>適應性評估表</span>
            </h2>
          </v-col>
          <v-col cols="12" md="4">
            <!-- 功能表 -->
            <v-toolbar dense>
              <v-text-field
                hide-details
                prepend-icon="mdi-magnify"
                single-line
                label="搜尋"
                v-model="serachKey"
                @keyup.stop="search()"
              ></v-text-field>

              <popupadd
                :viewData="viewData"
                @getAllData="getAllData"
                ref="editProcess"
                v-model="edititem"
              ></popupadd>

            </v-toolbar>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-text>
        <v-card class="mt-2">
          <v-card-text>
            <!-- 表單內容 -->
            <v-data-iterator
              :items="viewData"
              :items-per-page.sync="itemsPerPage"
              :footer-props="{ itemsPerPageOptions }"
            >
              <template v-slot:default="props">
                <v-simple-table fixed-header class="mt-2 text-no-wrap">
                  <template v-slot:default>
                    <thead style="background-color: #e3f2fd;">
                      <tr>
                        <th ></th>
                        <th >評估日期</th>
                        <th >1.入住意願接受度</th>
                        <th >2.家屬配合度</th>
                        <th >3.情緒</th>
                        <th >4.行為</th>
                        <th >5.社會化(參與度)</th>
                        <th >6.家庭支持度</th>
                        <th >總分</th>
                        <th >結果說明</th>
                        <th >紀錄人</th>
                        <th >修改紀錄</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item,index) in props.items" :key="index">
                        <td>
                          <v-menu transition="scale-transition" offset-y>
                            <template v-slot:activator="{ on }">
                              <v-btn color="primary" dark v-on="on">功能</v-btn>
                            </template>
                            <v-list>
                              <v-list-item @click="edit(item)">
                                <v-list-item-title>修改</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click="del(item)">
                                <v-list-item-title>刪除</v-list-item-title>
                              </v-list-item>
                              <v-list-item @click.stop="print(item)">
                                <v-list-item-title>列印</v-list-item-title>
                              </v-list-item>
                            </v-list>
                          </v-menu>
                        </td>
                        <td>{{item.date}}</td>
                        <td>{{item.datalist.question1 | checkEmpty}}</td>
                        <td>{{item.datalist.question2 | checkEmpty}}</td>
                        <td>{{item.datalist.question3 | checkEmpty}}</td>
                        <td>{{item.datalist.question4 | checkEmpty}}</td>
                        <td>{{item.datalist.question5 | checkEmpty}}</td>
                        <td>{{item.datalist.question6 | checkEmpty}}</td>
                        <td>{{item.datalist.total}}</td>
                        <td>{{item.datalist.result_description}}</td>
                        
                        <td>{{item.create_man}}</td>
                        <td>{{item.edit_man}}</td>
                      </tr>
                    </tbody>
                  </template>
                </v-simple-table>
              </template>
            </v-data-iterator>
          </v-card-text>
        </v-card>
      </v-card-text>
    </v-card>

    <!-- <pre>{{viewData}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/document/AdaptabilityAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      viewData: [], //顯示的資料
      serachKey: "",
      edititem: {}, //修改中資料
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10
    };
  },

  filters:{
    checkEmpty(val) {
      if (val == undefined){
        return "";
      }else{
        return val.text;
      }
    }
  },

  mounted() {
    this.getAllData();
  },

  methods: {
    getAllData() {
      var postData = {
        key: "u_snkey",
        value: this.$store.state.uData.snkey
      };

      //取得所有資料
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post("general/getByKey/" + this.$store.state.databaseName + "/adaptability",qs.stringify(postData))
        .then(rs => {
          console.log(rs.data)  
          this.items = this.viewData = rs.data.map(item=>{
            item['datalist'] = JSON.parse(item.datalist);
            return item;
          });
          
          // this.$store.commit("setLoadingOff"); //讀取畫面off
          this.sortBy();
        });
    },
    sortBy() {
      //依name排序
      this.viewData = this.viewData.sort(function(a, b) {
        return parseInt(a.date) < parseInt(b.date) ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.edititem = item
      this.$refs.editProcess.$emit("fromParent");
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then(res => {
        if (res) {
          this.$store.commit("getTime"); //取得現在時間
          delData.delman =
            this.$store.state.pData.username +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";
          var postData = {
            snkey: delData.snkey,
            tablename: "adaptability",
            info: JSON.stringify(delData)
          };

          this.axios
            .post(
              "general/delv3/" + this.$store.state.databaseName + "/adaptability",
              qs.stringify(postData)
            )
            .then(rs => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.getAllData();
              }
            });
        }
      });
    },
    search() {
      var searchArr = this.serachKey.split(" ");
      var outputitems = [...this.items];
      var str = "";
      searchArr.forEach(key => {
        if (key != "") {
          outputitems = outputitems.filter(item => {
            str = JSON.stringify(item).toUpperCase();
            if (str.includes(key.toUpperCase())) {
              return item;
            }
          });
        }
      });
      this.viewData = outputitems;
    },
    print(item) {
      // var printData = {
      //   medicalData: item,
      //   medicineData: medicineData
      // };

      //送出資料到列印頁面
      sessionStorage.setItem("printData", JSON.stringify(item));
      let routeData = this.$router.resolve({
        path: "/print/adaptabilityprint"
      });
      window.open(routeData.href, "_blank");
    }
  }
};
</script>


<style lang="scss">
.adaptabilitylist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
    }
  }
}
</style>