<template>
  <div class="adaptabilityadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <table class="table table-bordered table-sm">
              <thead class="form_title grey lighten-4">
                <tr>
                  <td class="text-center align-middle">評估日期</td>
                  <td>
                    <v-text-field label="評估日期" v-model="list.date" type="date" hide-details :rules="emptyRules"></v-text-field>
                  </td>
                </tr>
                <tr>
                  <th>評估項目</th>
                  <th>結果</th>
                </tr>
              </thead>

              <tr>
                <td class="text-left">1.入住意願接受度</td>
                <td>
                  <v-combobox :items="question1Items" v-model="datalist.question1" hide-details></v-combobox>
                </td>
              </tr>
              <tr>
                <td class="text-left">2.家屬配合度</td>
                <td>
                  <v-combobox :items="question2Items" v-model="datalist.question2" hide-details></v-combobox>
                </td>
              </tr>
              <tr>
                <td class="text-left">3.情緒</td>
                <td>
                  <v-combobox :items="question3Items" v-model="datalist.question3" hide-details></v-combobox>
                </td>
              </tr>
              <tr>
                <td class="text-left">4.行為</td>
                <td>
                  <v-combobox :items="question4Items" v-model="datalist.question4" hide-details></v-combobox>
                </td>
              </tr>
              <tr>
                <td class="text-left">5.社會化(參與度)</td>
                <td>
                  <v-combobox :items="question5Items" v-model="datalist.question5" hide-details></v-combobox>
                </td>
              </tr>
              <tr>
                <td class="text-left">6.家庭支持度</td>
                <td>
                  <v-combobox :items="question6Items" v-model="datalist.question6" hide-details></v-combobox>
                </td>
              </tr>
              <!-- <tr class="font-weight-bold">
                <td class="text-left">
                  評估結果:
                  <br />
                </td>
                <td class="d-flex align-center">
                  <v-text-field
                    label="總分"
                    v-model="total"
                    hide-details
                    readonly
                    class="form_title"
                    style="font-color:red"
                  ></v-text-field>
                </td>
              </tr>-->
            </table>

            <v-alert color="error" border="left" outlined>
              <v-sheet color="error dark-1" class="pa-2 white--text" elevation="4">
                <h2 class="d-flex">
                  評估結果:
                  <v-spacer></v-spacer>
                  <span>總分:{{total}} 分</span>
                </h2>
              </v-sheet>
            </v-alert>

            <v-alert color="success" border="left" outlined>
              <v-sheet color="success dark-1" class="pa-2 white--text" elevation="4">
                <h2>結果說明</h2>
              </v-sheet>
              <v-row>
                <v-col cols="12">
                  <v-textarea rows="8" filled v-model="datalist.result_description" hide-details></v-textarea>
                </v-col>
              </v-row>
            </v-alert>

            <!-- <v-row>
              <v-col cols="6">
                <v-text-field label="評估日期" v-model="list.date" type="date" :rules="emptyRules"></v-text-field>
              </v-col>
              <v-col cols="6"></v-col>
              <v-col cols="3">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>1.入住意願接受度</h2>
                  </v-sheet>
                  <v-combobox :items="question1Items" v-model="datalist.question1" hide-details></v-combobox>
                </v-alert>
              </v-col>
              <v-col cols="3">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>2.家屬配合度</h2>
                  </v-sheet>
                  <v-combobox :items="question2Items" v-model="datalist.question2" hide-details></v-combobox>
                </v-alert>
              </v-col>
              <v-col cols="3">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>3.情緒</h2>
                  </v-sheet>
                  <v-combobox :items="question3Items" v-model="datalist.question3" hide-details></v-combobox>
                </v-alert>
              </v-col>
              <v-col cols="3">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>4.行為</h2>
                  </v-sheet>
                  <v-combobox :items="question4Items" v-model="datalist.question4" hide-details></v-combobox>
                </v-alert>
              </v-col>
              <v-col cols="3">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>5.社會化(參與度)</h2>
                  </v-sheet>
                  <v-combobox :items="question5Items" v-model="datalist.question5" hide-details></v-combobox>
                </v-alert>
              </v-col>
              <v-col cols="3">
                <v-alert color="primary" border="left" outlined>
                  <v-sheet color="primary dark-1" class="pa-2 white--text" elevation="4">
                    <h2>6.家庭支持度</h2>
                  </v-sheet>
                  <v-combobox :items="question6Items" v-model="datalist.question6" hide-details></v-combobox>
                </v-alert>
              </v-col>
              
              <v-col cols="3">
                <v-alert color="error" border="left" outlined>
                  <v-sheet color="error dark-1" class="pa-2 white--text" elevation="4">
                    <h2>總分</h2>
                  </v-sheet>
                  <v-text-field v-model="total" hide-details readonly></v-text-field>
                </v-alert>
              </v-col>

              <v-col cols="12">
                <v-alert color="success" border="left" outlined>
                  <v-sheet color="success dark-1" class="pa-2 white--text" elevation="4">
                    <h2>結果說明</h2>
                  </v-sheet>
                  <v-row>
                    <v-col cols="12">
                      <v-textarea
                        rows="8"
                        filled
                        v-model="datalist.result_description"
                        hide-details
                      ></v-textarea>
                    </v-col>
                  </v-row>
                </v-alert>
              </v-col>
            </v-row>-->
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType=='add'"
            :loading="loading"
            :disabled="loading"
          >確認新增</v-btn>
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType=='edit'"
            :loading="loading"
            :disabled="loading"
          >確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{datalist}}</pre>  -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      datalist: {},
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      question1Items: [
        { score: 0, text: "無法評估" },
        { score: 1, text: "不願意" },
        { score: 2, text: "無意見" },
        { score: 3, text: "可接受" },
      ],
      question2Items: [
        { score: 0, text: "無家屬" },
        { score: 1, text: "欠佳" },
        { score: 2, text: "需溝通" },
        { score: 3, text: "良好" },
      ],
      question3Items: [
        { score: 0, text: "暴躁" },
        { score: 0, text: "無法評估" },
        { score: 1, text: "鬱悶" },
        { score: 2, text: "有起伏" },
        { score: 3, text: "穩定" },
      ],
      question4Items: [
        { score: 0, text: "衝動" },
        { score: 0, text: "無法評估" },
        { score: 1, text: "不穩" },
        { score: 2, text: "偶違常" },
        { score: 3, text: "正常" },
      ],
      question5Items: [
        { score: 0, text: "拒絕" },
        { score: 0, text: "無法評估" },
        { score: 1, text: "較被動需帶領" },
        { score: 2, text: "需鼓勵" },
        { score: 3, text: "主動" },
      ],
      question6Items: [
        { score: 0, text: "失聯" },
        { score: 1, text: "偶爾訪" },
        { score: 2, text: "月訪" },
        { score: 3, text: "週訪" },
      ],
      loading: false,
    };
  },
  props: ["edititem", "viewData"],

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function () {
      this.dialog = true;
      this.editProcess();
    });
  },

  model: {
    prop: "edititem",
    // event: "parentData"
  },

  watch: {
    edititem: [
      {
        handler: "editProcess",
      },
    ],
  },

  computed: {
    total() {
      // console.log('run total')
      var total = 0;
      for (var i in this.datalist) {
        //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
        // console.log(this.datalist[i]);
        if (this.datalist[i] != null) {
          if (this.datalist[i].score != undefined) {
            total = total + this.datalist[i].score;
          }
        }
      }
      return total;
    },
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      // this.list = {};

      //設定最後一筆資料為新增時的預設值及清除新增中不必要的值
      this.list = { ...this.viewData[0] };
      this.list.snkey = "";
      this.list.date = "";
      if (this.list.datalist != undefined) {
        this.datalist = { ...this.list.datalist };
      }
      this.list.edit_man = "";
    },
    editProcess() {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = this.edititem; //將頁面資料與父項的資料連結
      this.list = { ...this.edititem }; //複制一份實際修改資料內容
      this.datalist = this.list.datalist;
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.list.u_snkey = this.$store.state.uData.snkey;
        this.datalist["total"] = this.total;
        this.list.datalist = JSON.stringify(this.datalist);
        // this.list.total = this.total;
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
     
        this.axios
          .post(
            "general/add/" + this.$store.state.databaseName + "/adaptability",
            qs.stringify(this.list)
          )
          .then((rs) => {
            // console.log(rs.data)
            if (rs.data.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        this.datalist["total"] = this.total;
        this.list.datalist = JSON.stringify(this.datalist);
        // this.list.total = this.total;
        this.list["edit_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.loading = true;
        this.axios
          .post(
            "general/edit/" + this.$store.state.databaseName + "/adaptability",
            qs.stringify(this.list)
          )
          .then((rs) => {
            if (rs.data.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
              this.oldList["datalist"] = this.datalist;
              this.loading = false;
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";

.form_title {
  font-size: 1.2rem;
  text-align: center;
}
td {
  font-size: 1.2rem;
}
.vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
}
</style>